<template>
  <div id="app">
    <el-container direction="vertical">
      <div class="bg-img">
        <img src="./assets/img/background//banner_1.png" alt="" />
      </div>
      <el-header>
        <el-menu
          mode="horizontal"
          background-color="#222"
          text-color="#fff"
          active-text-color="#ffd04b"
          :default-active="$route.path"
          :router="true"
        >
          <el-menu-item index="/"></el-menu-item>
          <el-menu-item index="/news"></el-menu-item>
          <el-menu-item index="/game/library"></el-menu-item>
          <el-menu-item index="/login" v-if="!loginState" class="login-item"></el-menu-item>
          <el-submenu index="#" v-if="loginState" class="login-item">
            <template slot="title" class="login-item-title">
              <!--  -->
              <img class="profile-photo" :src="userData.profile_photo" alt="" />
              {{ userData.username }}
            </template>
            <el-menu-item :index="`/userData/${userData._id}`"></el-menu-item>
            <el-menu-item :index="`/updatePassword/${userData._id}`"></el-menu-item>
            <el-menu-item index="#" @click="logout"></el-menu-item>
          </el-submenu>
        </el-menu>
      </el-header>
      <el-main>
        <router-view />
      </el-main>
    </el-container>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'App',
  data() {
    return {};
  },
  computed: {
    ...mapState(['loginState', 'userData']),
  },
  methods: {
    // 
    async checkLogin() {
      if (localStorage.token) {
        try {
          // 
          const { data } = await this.$http.get(`/checkLogin`);
          const userData = data;
          // state
          this.$store.commit('changeUserData', userData);
        } catch (err) {
          console.log(err);
        }
      }
    },
    // 
    logout() {
      localStorage.clear();
      this.$router.go(0);
    },
  },
  created() {
    this.checkLogin();
  },
};
</script>

<style lang="scss" scoped>
.bg-img {
  position: fixed;
  left: 0;
  right: 0;
  z-index: -1;
  display: flex;
  justify-content: center;
  img {
    z-index: 0;
    min-width: 1920px;
    width: 100%;
    min-height: 100vh;
  }
}
.el-header {
  position: fixed;
  width: 100%;
  z-index: 100;
  background-color: #222;
  .el-menu {
    width: 1100px;
    margin: 0 auto;
    .login-item {
      float: right;
      .profile-photo {
        width: 30px;
        height: 30px;
        border-radius: 50%;
      }
    }
  }
}
.el-main {
  width: 1200px;
  margin: 60px auto;
}
</style>
<style lang="scss">
* {
  margin: 0;
  padding: 0;
}
html,
body {
  width: 100%;
  height: 100%;
}
</style>
