<template>
  <div class="gameDetail-container">
    <!--  -->
    <el-card class="game-main">
      <div slot="header" class="game-title">
        <h1>{{ gameDetail.game_name }}</h1>
        <p>{{ gameDetail.origin_name }}</p>
      </div>
      <div class="game-content">
        <!-- swiper -->
        <div class="game-picture">
          <swiper
            class="swiper-main"
            :modules="modules"
            :pagination="{ clickable: true }"
            :options="swiperOptionMain"
            ref="swiperMain"
          >
            <swiper-slide v-for="item in gameDetail.game_img_list" :key="item">
              <img :src="item" alt="" />
            </swiper-slide>
            <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
            <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
          </swiper>
          <swiper
            class="swiper-thumbs"
            :modules="modules"
            :pagination="{ clickable: true }"
            :options="swiperOptionThumbs"
            ref="swiperThumbs"
          >
            <swiper-slide v-for="item in gameDetail.game_img_list" :key="item">
              <img :src="item" alt="" />
            </swiper-slide>
          </swiper>
        </div>
        <div class="game-info">
          <img class="game-cover" :src="gameDetail.game_cover" alt="" />
          <div class="game-descri">
            <div class="game-score">
              <span></span>
              <span class="score" v-if="gameDetail.game_score">{{ gameDetail.game_score }}</span>
              <span class="score none" v-else></span>
            </div>
            <div class="platform">
              <span></span>
              <div class="tag-box">
                <el-tag
                  v-for="item in gameDetail.platform"
                  :key="item"
                  effect="plain"
                  size="medium "
                >
                  {{ item }}
                </el-tag>
              </div>
            </div>
            <div class="game-type">
              <span></span>
              <div class="tag-box">
                <el-tag
                  v-for="item in gameDetail.game_type"
                  :key="item"
                  effect="plain"
                  size="medium "
                >
                  {{ item }}
                </el-tag>
              </div>
            </div>
            <div class="game-date">
              <span></span>
              <div class="date">
                <span>{{ gameDetail.game_date }}</span>
              </div>
            </div>
            <div class="game-developer">
              <span></span>
              <div class="developer">
                <span>{{ gameDetail.game_developer }}</span>
              </div>
            </div>
            <div class="game-publisher">
              <span></span>
              <div class="publisher">
                <span>{{ gameDetail.game_publisher }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="game-brief">
        <h2 class="game-brief-title"></h2>
        <p class="game-brief-context">{{ gameDetail.game_brief }}</p>
      </div>
    </el-card>
    <!--  -->
    <!--  -->
    <evaluation @getGameDetail="getGameDetail" />
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper';
import Evaluation from '../../components/Evaluation/Evaluation.vue';

export default {
  components: {
    Swiper,
    SwiperSlide,
    Evaluation,
  },
  name: 'GameDetail',
  data() {
    return {
      // 
      gameDetail: {},
      // swiper
      swiperOptionMain: {
        spaceBetween: 10,
        loop: true,
        loopedSlides: 5,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
      },
      // swiper
      swiperOptionThumbs: {
        spaceBetween: 10,
        loop: true,
        loopedSlides: 5,
        slidesPerView: 'auto',
        slideToClickedSlide: true,
      },
    };
  },
  methods: {
    async getGameDetail() {
      try {
        const { data } = await this.$http.get(`/rest/gameInfo/details/${this.$route.params.id}`);
        this.gameDetail = data;
      } catch (err) {
        console.log(err);
      }
    },
  },
  created() {
    this.getGameDetail();
  },
  mounted() {
    // 
    document.documentElement.scrollTop = 0;
    // swiperMain  swiperThumbs 
    this.$nextTick(() => {
      const swiperMain = this.$refs.swiperMain.$swiper;
      const swiperThumbs = this.$refs.swiperThumbs.$swiper;
      swiperMain.controller.control = swiperThumbs;
      swiperThumbs.controller.control = swiperMain;
    });
  },
};
</script>

<style lang="scss" scoped>
.game-title * {
  margin: 5px 0;
}
.game-content {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}
// 
.game-picture {
  width: 800px;
  background-color: #000100;
  img {
    width: 100%;
  }
}
.swiper-thumbs {
  & .swiper-slide {
    width: 20%;
    opacity: 0.55;
  }
  & .swiper-slide-active {
    opacity: 1;
  }
}
// 
.game-info {
  max-width: 300px;
}
.game-cover {
  width: 100%;
}
.game-descri > * {
  display: flex;
  margin: 20px 0;
  & > span {
    margin-right: 30px;
    font-weight: bold;
  }
}
.score {
  font-size: 60px;
  color: #f5ba49;
}
.none {
  font-size: 24px;
  color: #333;
}
.tag-box {
  max-width: 200px;
  .el-tag--plain {
    margin: 2px;
  }
}
// 
.game-brief * {
  margin: 10px 0;
}
</style>
