<template>
  <div class="updatePassword-container">
    <el-card class="updatePassword-card">
      <div slot="header" class="header">
        <span></span>
      </div>
      <el-form
        ref="passwordForm"
        :model="userData"
        :rules="rules"
        @submit.native.prevent="updatePassword('passwordForm')"
      >
        <el-form-item label="" prop="password">
          <el-input
            type="password"
            v-model="userData.password"
            show-password
            placeholder=""
          />
        </el-form-item>
        <el-form-item label="" prop="checkPass">
          <el-input
            type="password"
            v-model="userData.checkPass"
            show-password
            placeholder=""
          />
        </el-form-item>
        <el-form-item class="button-item">
          <el-button type="primary" native-type="submit"></el-button>
          <el-button @click="$router.back()"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'UpdatePassword',
  data() {
    return {
      userData: {
        password: '',
        checkPass: '',
      },
      rules: {
        password: [{ required: true, message: '', trigger: 'blur' }],
        checkPass: [{ validator: this.validateCheckPass, trigger: 'blur' }],
      },
    };
  },
  methods: {
    validateCheckPass(rule, value, cb) {
      if (!value) cb(new Error(''));
      else if (value !== this.userData.password) cb(new Error(''));
      else cb();
    },
    // 
    updatePassword(formName) {
      // 
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // 
          try {
            await this.$http.put(`/rest/users/${this.$route.params.id}`, this.userData);
            this.$alert('', {
              type: 'success',
              showClose: false,
            }).then(() => {
              //  token
              localStorage.clear();
              // 
              this.$router.push('/login');
              this.$router.go(0);
            });
          } catch (err) {
            console.log(err);
          }
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
@import '@/assets/scss/user_form.scss';
</style>
