<template>
  <div class="userData-container">
    <el-card class="userData-card">
      <div slot="header" class="header">
        <span></span>
      </div>
      <el-descriptions title="">
        <el-descriptions-item label="">{{ userData.username }}</el-descriptions-item>
        <el-descriptions-item label="">{{ userType }}</el-descriptions-item>
        <el-descriptions-item label="">{{ banState }}</el-descriptions-item>
      </el-descriptions>
      <p style="font-weight: 900"></p>
      <el-form
        ref="userData"
        :model="userData"
        :rules="rules"
        @submit.native.prevent="editUserData('userData')"
      >
        <el-form-item label="" prop="nickname">
          <el-input v-model="userData.nickname" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="profile-photo" class="profile-item">
          <el-upload
            class="profile-uploader"
            ref="cover_uploader"
            :action="$http.defaults.baseURL + '/upload/profile_photo'"
            :headers="getAuthHeaders()"
            :show-file-list="false"
            :on-change="profileHandleChange"
            :on-success="afterUpload"
            :auto-upload="false"
          >
            <img v-if="tempImgUrl" :src="tempImgUrl" class="profile-photo" />
            <i v-else class="el-icon-plus profile-uploader-icon"></i>
          </el-upload>
          <el-button size="small" type="primary" @click="profileUpload"></el-button>
        </el-form-item>
        <el-form-item class="button-item">
          <el-button type="primary" native-type="submit"></el-button>
          <el-button @click="$router.push('/')"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'UserData',
  data() {
    return {
      tempImgUrl: '',
      userData: {
        username: '',
        nickname: '',
        profile_photo: '',
        type: '',
        isBan: Boolean,
      },
      rules: {
        nickname: [{ required: true, message: '', trigger: 'blur' }],
      },
    };
  },
  computed: {
    userType() {
      return this.userData.type ? '' : '';
    },
    banState() {
      return this.userData.isBan ? '' : '';
    },
  },
  methods: {
    // 
    async getUserData() {
      try {
        const result = await this.$http.get(`/rest/users/details/${this.$route.params.id}`);
        this.userData = result.data;
        // 
        this.tempImgUrl = this.userData.profile_photo;
      } catch (err) {
        console.log(err);
      }
    },
    // 
    getAuthHeaders() {
      return {
        Authorization: `Bearer ${localStorage.token || ''}`,
      };
    },
    // 
    profileHandleChange(file, fileList) {
      // 
      if (fileList.length > 1) {
        fileList.shift(); //  
      }
      // 
      this.tempImgUrl = URL.createObjectURL(file.raw);
    },
    // 
    profileUpload() {
      this.$refs.cover_uploader.submit();
      this.$message({
        message: '',
        type: 'success',
      });
    },
    // 
    afterUpload(res) {
      this.userData.profile_photo = res.url;
    },
    validateCheckPass(rule, value, cb) {
      if (!value) cb(new Error(''));
      else if (value !== this.userData.password) cb(new Error(''));
      else cb();
    },
    // 
    editUserData(formName) {
      // 
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // 
          try {
            await this.$http.put(`/rest/users/${this.$route.params.id}`, this.userData);
            this.$message({
              message: '',
              type: 'success',
            });
          } catch (err) {
            console.log(err);
          }
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
  },
  created() {
    this.getUserData();
  },
};
</script>

<style lang="scss" scoped>
@import '@/assets/scss/user_form.scss';
.userData-card {
  width: 50rem;
}
.profile-item {
  display: flex;
  /deep/ .el-form-item__content {
    display: flex;
    flex-direction: column;
  }
}
.profile-uploader {
  /deep/ .el-upload {
    border: 1px dashed #b4bccc;
    border-radius: 50%;
    cursor: pointer;
    overflow: hidden;
  }
}
.profile-photo {
  width: 188px;
  height: 188px;
  display: block;
}
</style>
