<template>
  <div :class="`news-container ${type}`">
    <el-card class="news-card">
      <div slot="header" class="header">
        <span class="title"></span>
        <router-link class="show-more-link" to="/news" v-if="showMore">
          <span></span>
          <i class="el-icon-d-arrow-right"></i>
        </router-link>
      </div>
      <!--  -->
      <item-card v-for="item in articlesData" :key="item._id" :path="`/new/${item._id}`">
        <template #img-box>
          <img class="new-cover" :src="item.cover" />
        </template>
        <template #item-header>
          <p class="new-title" :title="item">
            {{ item.title }}
          </p>
        </template>
        <template #item-main>
          <p class="new-abstract">
            {{ item.content }}
          </p>
        </template>
        <template #item-footer>
          <div class="new-footer" v-if="showFooter">
            <p class="new-author"></p>
            <p class="new-date">{{ item.publishTime }}</p>
          </div>
        </template>
      </item-card>
      <!--  -->
      <div class="paging" v-if="showPaging">
        <el-pagination
          layout="prev, pager, next, jumper, ->, total"
          :total="articlesTotal"
          background
          @current-change="currentChange"
        />
      </div>
    </el-card>
  </div>
</template>

<script>
import ItemCard from './Item-card.vue';

export default {
  name: 'News',
  components: {
    ItemCard,
  },
  //  footer
  props: {
    type: { type: String, default: '' },
    showMore: { type: Boolean, default: false },
    showFooter: { type: Boolean, default: true },
    showPaging: { type: Boolean, default: true },
  },
  data() {
    return {
      articlesData: [],
      articlesTotal: 0,
      articlesListPage: 1,
    };
  },
  methods: {
    // 
    async getNewsData() {
      try {
        const { data } = await this.$http.get(
          `/rest/articles?page=${this.articlesListPage}&sort=pubTimeDesc`
        );
        this.articlesData = data.result;
        this.articlesTotal = data.total;
      } catch (err) {
        console.log(err);
      }
    },
    // 
    currentChange(currentPage) {
      this.articlesListPage = currentPage;
      // 
      this.getNewsData({ page: this.articlesListPage });
      // 
      document.documentElement.scrollTop = 0;
    },
  },
  created() {
    this.getNewsData();
  },
};
</script>

<style lang="scss" scoped>
.news-card {
  background-color: #f8f8f9;
}
.header {
  .title {
    font-size: 20px;
    font-weight: 600;
  }
  .show-more-link {
    float: right;
    text-decoration: none;
    span,
    i {
      font-size: 14px;
      color: #646464;
    }
  }
}
.new-cover {
  width: 192px;
  height: 108px;
}
.new-title {
  margin: 5px 0;
  overflow: hidden;
  //  1 
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.new-abstract {
  margin: 5px 0;
  font-size: 14px;
  color: #646464;
  overflow: hidden;
  //  2 
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.new-footer {
  display: flex;
  justify-content: space-between;
  .new-author,
  .new-date {
    font-size: 12px;
    color: #969696;
  }
}
.paging {
  margin-top: 20px;
}
// 
.news-container.side {
  width: 400px;
  /deep/ .news-card {
    .new-cover {
      width: 96px;
      height: 72px;
    }
    .new-title {
      font-size: 15px;
    }
  }
}
</style>
