<template>
  <div class="login-container">
    <el-card class="login-card">
      <div slot="header" class="header">
        <span></span>
      </div>
      <el-form
        ref="loginUser"
        :model="userData"
        :rules="rules"
        @submit.native.prevent="login('loginUser')"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="userData.username" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            type="password"
            v-model="userData.password"
            show-password
            placeholder=""
          />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" native-type="submit"></el-button>
        </el-form-item>
        <el-form-item class="register">
          <router-link to="/register"></router-link>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      userData: {
        username: '',
        password: '',
      },
      rules: {
        username: [{ required: true, message: '', trigger: 'blur' }],
        password: [{ required: true, message: '', trigger: 'blur' }],
      },
    };
  },
  // 
  beforeRouteLeave(to, from, next) {
    console.log(to);
    if (to.name === 'UpdatePassword') next('/');
    else next();
  },
  methods: {
    async login(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          try {
            // 
            const result = await this.$http.post('/webLogin', this.userData);
            // tokenlocalStorage
            localStorage.token = result.data;
            this.$message({
              message: '',
              type: 'success',
            });
            // 
            const { data } = await this.$http.get(`/checkLogin`);
            const userData = data;
            // state
            this.$store.commit('changeUserData', userData);
            this.$router.back();
          } catch (err) {
            console.log(err);
          }
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
@import '@/assets/scss/user_form.scss';
</style>
