<template>
  <div class="register-container">
    <el-card class="register-card">
      <div slot="header" class="header">
        <span></span>
      </div>
      <el-form
        ref="registerUser"
        :model="userData"
        :rules="rules"
        @submit.native.prevent="registerUser('registerUser')"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="userData.username" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="nickname">
          <el-input v-model="userData.nickname" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            type="password"
            v-model="userData.password"
            show-password
            placeholder=""
          />
        </el-form-item>
        <el-form-item label="" prop="checkPass">
          <el-input
            type="password"
            v-model="userData.checkPass"
            show-password
            placeholder=""
          />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" native-type="submit"></el-button>
        </el-form-item>
        <el-form-item class="login">
          <router-link to="/login"></router-link>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Register',
  data() {
    return {
      userData: {
        username: '',
        nickname: '',
        password: '',
        checkPass: '',
      },
      rules: {
        username: [{ required: true, message: '', trigger: 'blur' }],
        nickname: [{ required: true, message: '', trigger: 'blur' }],
        password: [{ required: true, message: '', trigger: 'blur' }],
        checkPass: [{ validator: this.validateCheckPass, trigger: 'blur' }],
      },
    };
  },
  methods: {
    validateCheckPass(rule, value, cb) {
      if (!value) cb(new Error(''));
      else if (value !== this.userData.password) cb(new Error(''));
      else cb();
    },
    async registerUser(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          try {
            await this.$http.post('/rest/users/add', this.userData);
            this.$message({
              message: '',
              type: 'success',
            });
            this.$router.push('/');
          } catch (err) {
            console.log(err);
          }
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
@import '@/assets/scss/user_form.scss';
</style>
